import { Collapse } from "antd";
import { CInput, CSelect, CSwitch, CDatePicker, CInputNumber } from "@components";

const CFixAssetInsurance= ()=>{
   const { Panel } = Collapse
   return (
      <>
         <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10 }}>
            <Panel header="Insurance" key="0">
               <fieldset>
                  <legend>INSURANCE</legend>
                  <CSelect
                     label="Insurance Vendor"
                     name="insurance_vendor"
                     id="insurance_vendor"
                     isRequired
                     autoFocus
                  />

                  <CInput
                     label="Agent"
                     name="agent"
                     id="agent"
                     isRequired
                     autoFocus
                  />
               </fieldset>

               <fieldset>
                  <legend>POLICY</legend>
                  <CInput
                     label="Policy Number"
                     name="policy_number"
                     id="policy_number"
                     isRequired
                     autoFocus
                  />

                  <CDatePicker
                     label="Policy Expiration Date"
                     name="policy_expiration_date"
                     id="policy_expiration_date"
                     isRequired
                     autoFocus
                  />

                  <CInputNumber
                     label="Policy Amount"
                     name="policy_amount"
                     id="policy_amount"
                     isRequired
                     autoFocus
                  />

                  <CInputNumber
                     label="Insured value"
                     name="insured_value"
                     id="insured_value"
                     isRequired
                     autoFocus
                  />

                  <CInputNumber
                     label="Replacement Cost"
                     name="replacement_const"
                     id="replacement_const"
                     isRequired
                     autoFocus
                  />

                  <CDatePicker
                     label="Last Periodic value/const"
                     name="last_periodic_value"
                     id="last_periodic_value"
                     isRequired
                     autoFocus
                  />

                  <CDatePicker
                     label="Insurance date 1"
                     name="insurance_date_1"
                     id="insurance_date_1"
                     isRequired
                     autoFocus
                  />

                  <CDatePicker
                     label="Insurance date 2"
                     name="insurance_date_2"
                     id="insurance_date_2"
                     isRequired
                     autoFocus
                  />

                  <CSwitch
                     label="Insured at fair market value"
                     name="insured_at_fair"
                     id="insured_at_fair"
                     isRequired
                     autoFocus
                  />
               </fieldset>
            </Panel>
         </Collapse>
      </>
   )
}
export default CFixAssetInsurance;